@use 'base/variable' as v; 
@use 'base/mixins' as m;

.header{
    background-color: v.$verde;

    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem 15px 100px;
        }
    }

    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;

        }
    }
}
.contenido-header{
    padding: 1rem;
    @include m.tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;

    }
}
.navegacion-principal{
    @include m.tablet {
        display: flex;
        gap: 1rem
    }
    a{
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0;
        }
        &:hover{
        color: v.$amarillo;
        }
    }
   
}
