@use 'base/variable' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;

    h3, p{
        text-align: center;
        color: v.$blanco;

    }

    .dia{
        text-transform: uppercase;
        margin-bottom: 5rem 0;
        
    }
}
.escenario-contenedor{
    @include m.tablet {
        @include m.grid(2, 5rem) ;                  
    }

    p{
        color: v.$negro;
    }
   
}

.escenario{
    padding-top: 5rem;

    p{

        &:after{
            content:'';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;

        }
    }
}

.edm .nombre-escenario:after{
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after{
    background-image: url(../img/icono_rock.png);
}

.calendario{
     @include m.resetear-lista;
     li{
         padding: 20px;
        font-weight: 700;//grosor de la letra
         span{
            font-weight: 400;
         }
    }        
}

.bg-amarillo{
    background-color: v.$amarillo;

    li:nth-child(odd){//selecion saltada/intercalda
    background-color: darken(v.$amarillo, 10%/*oscuridad*/);//darken hace mas oscuro el color
    }
}

.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){//selecion saltada/intercalda
        background-color: darken(v.$verde, 10%/*oscuridad*/);//darken hace mas oscuro el color
        }
}

