@use 'base/variable' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 50rem;
    min-height: 30rem;
    overflow: hidden;
    
    .overlay{
        position: absolute;
        height: 100%;
        width: 100%;
        background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b565da54 0%, #cf6cc849 33%, #ee609b65 66%, #ee609b6c 100%);    
        display: flex;
        flex-direction: column;
        justify-content: center ;
    }
    
        video{
         width: auto;
         display: block;
            @include m.desktop {
                width: 100%;
            }
        }

}

.contenido-video{
    z-index: 10;/*superponiendo seleccion que hay detras de*/  
    display: grid;
    place-items: center;
    h2{
        color: v.$blanco;
        font-size: 70px;
        margin: 0;
        padding: 1rem 4rem;
    }

    p{
        background-color: v.$rosa;
        max-width: 80%;
        display: inline-block;
        padding: 1rem 4rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}