@use 'variable' as v;

html{
    font-size: 62.5%;
    box-sizing: border-box;

}

*,
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 16px;

    &.fijar-body{
        overflow: hidden;
    }

    &.body-scroll{
        padding-top: 66px ;/*clave para no dar el salto en el scroll de la barra fija*/
    }
}

p{
    font-size: 20px;
    color: v.$negro;
    line-height: 1.5;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2, h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 40px;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 30px;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}