@use 'base/variable' as v;
@use 'base/mixins' as m;

.galeria{
   padding: 5rem 0;
   
   h3{
       text-align: center;
   }
}

.galeria-imagenes{
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.telefono {
        @include m.grid(3, 2rem);//grid es un mixin
    }
    @include m.tablet{
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}

.overlay{
    position: fixed;//fixed es fijo
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;
    background-color: hsla(0, 0%, 13%, 0.692);

    picture{
        display: block;
        max-width: 80rem;

    }
}

.btn-cerrar{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 900;

    &:hover {
        cursor: pointer;
    }
}